import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { last } from 'lodash';

import { RxGuidService } from '@helix/platform/utils';
import { IHttpGetParams, IHttpOptions, RX_BUNDLE } from '@helix/platform/shared/api';

import { IFieldDefinitionLight, IProcessDefinition } from './process-definition.types';

@Injectable({
  providedIn: 'root'
})
export class RxProcessDefinitionService {
  constructor(private httpClient: HttpClient, private rxGuidService: RxGuidService) {}

  get(processDefinitionName: string, options?: IHttpGetParams): Observable<IProcessDefinition> {
    return this.httpClient.get<IProcessDefinition>(this.getUrl(processDefinitionName), options);
  }

  getNew(): Observable<IProcessDefinition> {
    return of({
      allowOverlay: false,
      artifacts: [],
      contextKeyParam: null,
      description: '',
      flowElements: [],
      guid: this.rxGuidService.generate('rx-'),
      inputParams: [],
      isEnabled: true,
      lastChangedBy: null,
      lastUpdateTime: null,
      layout: JSON.stringify({ cells: [] }),
      localVariables: [],
      localizableStrings: {},
      name: '',
      outputParams: [],
      overlayDescriptor: null,
      overlayGroupId: null,
      owner: null,
      permissions: [],
      runAsUser: false,
      scope: RX_BUNDLE.definitionScopeTypes.bundle,
      synchronous: null
    });
  }

  getServerActionModelType(actionTypeName: string): string {
    return `rx.ProcessActions.${actionTypeName.replace(':', '.')}`;
  }

  getServerActionTypeName(modelType: string): string {
    return last(modelType.split('.'));
  }

  update(processDefinition: IProcessDefinition, options?: IHttpOptions): Observable<any> {
    return this.httpClient.put(this.getUrl(processDefinition.name), processDefinition, options);
  }

  getOutputParams(processDefinitionName: string, options?: IHttpGetParams): Observable<IFieldDefinitionLight[]> {
    return this.httpClient.get<IFieldDefinitionLight[]>(
      `/api/rx/application/process/processdefinition/${encodeURIComponent(processDefinitionName)}/outputParams`,
      options
    );
  }

  getInputParams(processDefinitionName: string, options?: IHttpGetParams): Observable<IFieldDefinitionLight[]> {
    return this.httpClient.get<IFieldDefinitionLight[]>(
      `/api/rx/application/process/processdefinition/${encodeURIComponent(processDefinitionName)}/inputParams`,
      options
    );
  }

  private getUrl(processDefinitionName?: string): string {
    return processDefinitionName
      ? `/api/rx/application/process/processdefinition/${encodeURIComponent(processDefinitionName)}`
      : '/api/rx/application/process/processdefinition';
  }
}
