import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { IProcessInstance, IProcessInstanceLog } from './process-instance.interfaces';

@Injectable({
  providedIn: 'root'
})
export class RxProcessInstanceService {
  constructor(private httpClient: HttpClient) {}

  get(processDefinitionName: string, processInstanceId: string): Observable<IProcessInstance> {
    return this.httpClient.get<IProcessInstance>(
      `/api/rx/application/process/processinstance/${encodeURIComponent(processDefinitionName)}/${processInstanceId}`
    );
  }

  getLog(processDefinitionName: string, processInstanceId: string): Observable<IProcessInstanceLog> {
    return this.httpClient.get<IProcessInstanceLog>(
      `/api/rx/application/process/processinstance/log/${encodeURIComponent(
        processDefinitionName
      )}/${processInstanceId}/view`
    );
  }

  downloadLog(processDefinitionName: string, processInstanceId: string): Observable<Blob> {
    return this.httpClient.get(
      `/api/rx/application/process/processinstance/log/${encodeURIComponent(
        processDefinitionName
      )}/${processInstanceId}/download`,
      {
        responseType: 'blob'
      }
    );
  }
}
