import { Injectable } from '@angular/core';
import { forkJoin, Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { filter, find, forEach, map as _map, pick } from 'lodash';
import {
  IDesignerStencilElement,
  RxActionTypeUtilsService,
  RxBundleCacheService,
  RxDefinitionNameService,
  RxGlobalCacheService
} from '@helix/platform/shared/api';
import { RxStringService } from '@helix/platform/utils';
import { RX_PROCESS_DEFINITION } from './process-definition.constant';
import { ElementVisibilityOptions, ICallActivityElement } from './process-element.types';
import { RxCallActivityRegistryService } from './call-activity-registry.service';

@Injectable({
  providedIn: 'root'
})
export class RxProcessElementsService {
  constructor(
    private rxActionTypeUtilsService: RxActionTypeUtilsService,
    private rxBundleCacheService: RxBundleCacheService,
    private rxCallActivityRegistryService: RxCallActivityRegistryService,
    private rxDefinitionNameService: RxDefinitionNameService,
    private rxGlobalCacheService: RxGlobalCacheService,
    private rxStringService: RxStringService
  ) {}

  getActionElements(actionResourceType: string): Observable<IDesignerStencilElement[]> {
    return forkJoin([
      this.rxBundleCacheService.getActionTypes(),
      this.rxGlobalCacheService.getBundleDescriptors()
    ]).pipe(
      map(([actionTypes, bundleDescriptors]) => {
        return _map(actionTypes, (actionType) => {
          return {
            group:
              this.rxActionTypeUtilsService.getActionTypeBundleFriendlyName(bundleDescriptors, actionType) ||
              'Platform actions',
            label:
              actionType.displayName || this.rxActionTypeUtilsService.prettifyActionTypeName(actionType.actionTypeName),
            value: {
              actionTypeName: actionType.actionTypeName,
              resourceType: actionResourceType
            }
          };
        });
      })
    );
  }

  getCallActivityElements(): Observable<IDesignerStencilElement[]> {
    return forkJoin([this.rxGlobalCacheService.getBundleDescriptors(), this.getVisibleCallActivities()]).pipe(
      map(([bundleDescriptors, visibleCallActivities]) => {
        const callActivityElements = [];

        forEach(visibleCallActivities, (visibleCallActivity) => {
          const bundle = find(bundleDescriptors, {
            id: this.rxDefinitionNameService.getBundleId(visibleCallActivity.processDefinitionName)
          });

          if (bundle) {
            callActivityElements.push({
              group: bundle.friendlyName,
              label:
                visibleCallActivity.displayName ||
                this.rxStringService.prettify(
                  this.rxDefinitionNameService.getDisplayName(visibleCallActivity.processDefinitionName)
                ),
              value: {
                resourceType: RX_PROCESS_DEFINITION.processElementResourceTypes.callActivity,
                calledProcessDefinitionName: visibleCallActivity.processDefinitionName
              }
            });
          }
        });

        return callActivityElements;
      })
    );
  }

  getProcessElements(): Observable<IDesignerStencilElement[]>[] {
    return [
      this.getActionElements(RX_PROCESS_DEFINITION.processElementResourceTypes.processAction),
      this.getCallActivityElements(),
      this.getStandardProcessElements()
    ];
  }

  getStandardProcessElements(): Observable<IDesignerStencilElement[]> {
    return of(
      _map(RX_PROCESS_DEFINITION.standardProcessElements, (element) => {
        let label;

        if (element.resourceType === RX_PROCESS_DEFINITION.processElementResourceTypes.processAction) {
          label = this.rxActionTypeUtilsService.prettifyActionTypeName(element.actionTypeName);
        } else if (element.eventResourceType === RX_PROCESS_DEFINITION.processElementResourceTypes.errorEvent) {
          const errorEventLabel =
            RX_PROCESS_DEFINITION.processElementNamesByResourceType[
              RX_PROCESS_DEFINITION.processElementResourceTypes.errorEvent
            ];

          const boundaryEventLabel =
            RX_PROCESS_DEFINITION.processElementNamesByResourceType[
              RX_PROCESS_DEFINITION.processElementResourceTypes.boundaryEvent
            ];

          const endEventLabel =
            RX_PROCESS_DEFINITION.processElementNamesByResourceType[
              RX_PROCESS_DEFINITION.processElementResourceTypes.endEvent
            ];

          label =
            element.resourceType === RX_PROCESS_DEFINITION.processElementResourceTypes.boundaryEvent
              ? `${errorEventLabel} ${boundaryEventLabel}`
              : `${errorEventLabel} ${endEventLabel}`;
        } else if (element.resourceType === RX_PROCESS_DEFINITION.processElementResourceTypes.boundaryEvent) {
          label = RX_PROCESS_DEFINITION.processElementNamesByResourceType[element.eventResourceType];
        } else {
          label = RX_PROCESS_DEFINITION.processElementNamesByResourceType[element.resourceType];
        }

        return {
          group: element.group,
          label: label,
          value: pick(element, RX_PROCESS_DEFINITION.processElementConfigurationProperties)
        };
      })
    );
  }

  getVisibleCallActivities(): Observable<ICallActivityElement[]> {
    return of(
      filter(Array.from(this.rxCallActivityRegistryService.getRegisteredCallActivities()), {
        visibility: ElementVisibilityOptions.Always
      })
    );
  }
}
