import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { combineLatest, Observable, ReplaySubject } from 'rxjs';
import { map, shareReplay, take } from 'rxjs/operators';
import { isEmpty, omit, reject } from 'lodash';

import { RxIdService, RxObjectUtilsService } from '@helix/platform/utils';
import {
  IDataDictionary,
  IDataDictionaryBranch,
  RxDataDictionaryUtils,
  RxDesignerCacheService,
  RxGlobalCacheService
} from '@helix/platform/shared/api';

import { IActivitiesDataDictionaryState } from './activities-data-dictionary-state.interface';

@Injectable({
  providedIn: 'root'
})
export class RxProcessDataDictionaryService {
  private commonActivities: IActivitiesDataDictionaryState = null;
  private commonActivitiesSubject = new ReplaySubject<IActivitiesDataDictionaryState>(1);

  commonActivities$ = this.commonActivitiesSubject.asObservable();

  private commonActivitiesDataDictionaryStateClone$ = this.commonActivities$.pipe(
    map((activitiesDataDictionaryState) =>
      activitiesDataDictionaryState ? this.rxObjectUtilsService.cloneDeep(activitiesDataDictionaryState) : {}
    ),
    shareReplay(1)
  );

  commonDataDictionary$ = this.getCommonDataDictionary();

  constructor(
    private rxDataDictionaryUtils: RxDataDictionaryUtils,
    private rxDesignerCacheService: RxDesignerCacheService,
    private rxGlobalCacheService: RxGlobalCacheService,
    private rxIdService: RxIdService,
    private rxObjectUtilsService: RxObjectUtilsService,
    private translateService: TranslateService
  ) {}

  clear(): void {
    this.setCommonActivities(null);
  }

  getCommonDataDictionary(): Observable<IDataDictionary> {
    return combineLatest([
      this.commonActivitiesDataDictionaryStateClone$.pipe(
        map((componentsDataDictionaryState) => Object.values(componentsDataDictionaryState))
      )
    ]).pipe(
      map(([activitiesDataDictionaryBranches]) => {
        return [
          {
            label: 'General',
            children: [
              {
                label: this.translateService.instant('com.bmc.arsys.rx.client.designer.keywords.current-date.label'),
                icon: 'd-icon-dollar',
                expression: '$DATE$'
              },
              {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.designer.keywords.current-date-time.label'
                ),
                icon: 'd-icon-dollar',
                expression: '$TIMESTAMP$'
              },
              {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.designer.keywords.current-group-ids.label'
                ),
                icon: 'd-icon-dollar',
                expression: '$GROUPIDS$'
              },
              {
                label: this.translateService.instant('com.bmc.arsys.rx.client.designer.keywords.current-groups.label'),
                icon: 'd-icon-dollar',
                expression: '$GROUPS$'
              },
              {
                label: this.translateService.instant('com.bmc.arsys.rx.client.designer.keywords.current-roles.label'),
                icon: 'd-icon-dollar',
                expression: '$ROLES$'
              },
              {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.designer.keywords.current-server-urls.label'
                ),
                icon: 'd-icon-dollar',
                expression: '$SERVERURL$'
              },
              {
                label: this.translateService.instant('com.bmc.arsys.rx.client.designer.keywords.current-time.label'),
                icon: 'd-icon-dollar',
                expression: '$TIME$'
              },
              {
                label: this.translateService.instant('com.bmc.arsys.rx.client.designer.keywords.current-user.label'),
                icon: 'd-icon-dollar',
                expression: '$USER$'
              },
              {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.designer.keywords.current-user-locale.label'
                ),
                icon: 'd-icon-dollar',
                expression: '$LOCALE$'
              },
              {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.designer.keywords.current-week-day.label'
                ),
                icon: 'd-icon-dollar',
                expression: '$WEEKDAY$'
              },
              {
                label: this.translateService.instant('com.bmc.arsys.rx.client.designer.keywords.new-line.label'),
                icon: 'd-icon-dollar',
                expression: '$NEWLINE$'
              },
              {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.process-designer.keywords.process-correlation-id.label'
                ),
                icon: 'd-icon-dollar',
                expression: '$PROCESSCORRELATIONID$'
              },
              {
                label: 'NULL',
                icon: 'd-icon-dollar',
                expression: '$NULL$',
                hidden: true
              }
            ]
          },
          {
            label: 'Functions',
            expanded: true,
            children: reject(
              this.rxDataDictionaryUtils.getFunctionDataDictionaryBranch(
                this.rxDesignerCacheService.getFunctionDescriptorsSync()
              ),
              { label: 'Rule Qualification' }
            )
          },
          {
            label: 'Activities',
            expanded: true,
            children: activitiesDataDictionaryBranches
          }
        ];
      })
    );
  }

  setCommonActivitiesDataDictionaryBranch(
    guid: string,
    activityDataDictionaryBranch: Observable<IDataDictionaryBranch>
  ): void {
    activityDataDictionaryBranch.pipe(take(1)).subscribe((dataDictionaryBranch) => {
      this.setCommonActivities(
        isEmpty(dataDictionaryBranch)
          ? omit(this.commonActivities, [guid])
          : { ...this.commonActivities, [guid]: dataDictionaryBranch }
      );
    });
  }

  private setCommonActivities(state: IActivitiesDataDictionaryState): void {
    this.commonActivities = state;

    this.commonActivitiesSubject.next(this.commonActivities);
  }
}
