import { Injectable } from '@angular/core';

import { IDesignerElementRegistry, RxLogService } from '@helix/platform/shared/api';

import { IProcessElementDescriptor } from './process-element-descriptor.interface';
import { RX_PROCESS_DEFINITION } from '../process-definition.constant';

declare var joint: any;

@Injectable({
  providedIn: 'root'
})
export class RxProcessElementRegistryService implements IDesignerElementRegistry {
  private elementDescriptors: Map<string, IProcessElementDescriptor> = new Map();

  constructor(private rxLogService: RxLogService) {}

  get(type: string): IProcessElementDescriptor {
    return (
      this.elementDescriptors.get(type) ??
      this.elementDescriptors.get(RX_PROCESS_DEFINITION.processElementTypes.processAction)
    );
  }

  getAll(): IProcessElementDescriptor[] {
    return Array.from(this.elementDescriptors.values());
  }

  register(descriptor: IProcessElementDescriptor): void {
    if (this.elementDescriptors.has(descriptor.type)) {
      this.rxLogService.warning(`Process element ${descriptor.type} is already registered.`);
    } else {
      joint.util.setByPath(joint.shapes.rx, descriptor.shapeType, descriptor.shapeClass, '.');
      joint.util.setByPath(joint.shapes.rx, descriptor.viewShapeType, descriptor.viewShapeClass, '.');

      this.elementDescriptors.set(descriptor.type, descriptor);
    }
  }
}
